export default class Frame {
  constructor(frames) {
    this.id = 0;
    this.signature = '';
    this.defaultBackgroundFrame = 'data:image/png;base64,';
    this.image = '';
    this.deleteButton = '';
    this.deleteButtonFirst = document.getElementById('0').children[2];
    this.deleteButtonFirst.addEventListener('click', (e) => {
      const downElement = e.currentTarget.parentElement.nextElementSibling;
      if (frames.children.length - 1 === 1) {
        return;
      }
      if (e.currentTarget.parentElement.classList.contains('active')) {
        downElement.classList.add('active');
      }
      e.currentTarget.parentElement.remove();
      this.id--;
      for (let i = 0; i < frames.children.length - 1; i = +1) {
        frames.children[i].id = i;
        frames.children[i].children[0].innerHTML = i;
      }
    });
    this.copyButton = '';
    this.positionClick = '';
    this.positionActive = '';
  }

  addAndDelete(frames) {
    const frame = document.getElementById(this.id);
    const newFrame = frame.cloneNode(true);
    this.image = newFrame.getElementsByTagName('img')[0];
    this.image.src = this.defaultBackgroundFrame;
    this.signature = newFrame.children[0];
    this.deleteButton = newFrame.children[2];
    this.copyButton = newFrame.children[3];
    newFrame.id = this.id + 1;
    this.signature.innerHTML = newFrame.id;
    for (let i = 0; i < frames.children.length - 1; i++) {
      frames.children[i].classList.remove('active');
    }
    this.deleteButton.addEventListener('click', (e) => {
      if (frames.children.length - 1 === 1) {
        return;
      }
      const topElement = e.currentTarget.parentElement.previousElementSibling;
      const downElement = e.currentTarget.parentElement.nextElementSibling;
      const Element = e.currentTarget.parentElement;
      this.id--;
      for (let i = 0; i < frames.children.length - 1; i++) {
        if (frames.children[i] === Element) {
          this.positionClick = i;
        }
        if (frames.children[i].classList.contains('active')) {
          this.positionActive = i;
        }
      }
      if (this.positionActive === this.positionClick) {
        if (this.positionActive === 0) {
          downElement.classList.add('active');
        } else {
          topElement.classList.add('active');
        }
        Element.remove();
        for (let i = 0; i < frames.children.length - 1; i++) {
          frames.children[i].id = i;
          frames.children[i].children[0].innerHTML = i;
        }
      }
      if (this.positionActive < this.positionClick) {
        Element.remove();
        for (let i = 0; i < frames.children.length - 1; i++) {
          frames.children[i].id = i;
          frames.children[i].children[0].innerHTML = i;
        }
      }
      if (this.positionActive > this.positionClick) {
        Element.remove();
        for (let i = 0; i < frames.children.length - 1; i++) {
          frames.children[i].id = i;
          frames.children[i].children[0].innerHTML = i;
        }
      }
    });
    newFrame.classList.add('active');
    frame.parentNode.insertBefore(newFrame, frame.nextSibling);
    this.id++;
  }
}
