module.exports = "/*\n * NeuQuant Neural-Net Quantization Algorithm\n * ------------------------------------------\n *\n * Copyright (c) 1994 Anthony Dekker\n *\n * NEUQUANT Neural-Net quantization algorithm by Anthony Dekker, 1994. See\n * \"Kohonen neural networks for optimal colour quantization\" in \"Network:\n * Computation in Neural Systems\" Vol. 5 (1994) pp 351-367. for a discussion of\n * the algorithm.\n *\n * Any party obtaining a copy of these files from the author, directly or\n * indirectly, is granted, free of charge, a full and unrestricted irrevocable,\n * world-wide, paid up, royalty-free, nonexclusive right and license to deal in\n * this software and documentation files (the \"Software\"), including without\n * limitation the rights to use, copy, modify, merge, publish, distribute,\n * sublicense, and/or sell copies of the Software, and to permit persons who\n * receive copies from any such party to do so, with the only requirement being\n * that this copyright notice remain intact.\n */\n\n/*\n * This class handles Neural-Net quantization algorithm\n * @author Kevin Weiner (original Java version - kweiner@fmsware.com)\n * @author Thibault Imbert (AS3 version - bytearray.org)\n * @author Kevin Kwok (JavaScript version - https://github.com/antimatter15/jsgif)\n * @version 0.1 AS3 implementation\n */\nNeuQuant = function () {\n  var exports = {};\n  var netsize = 256;\n  /* number of colours used */\n\n  /* four primes near 500 - assume no image has a length so large */\n\n  /* that it is divisible by all four primes */\n\n  var prime1 = 499;\n  var prime2 = 491;\n  var prime3 = 487;\n  var prime4 = 503;\n  var minpicturebytes = 3 * prime4;\n  /* minimum size for input image */\n\n  /*\n   * Program Skeleton ---------------- [select samplefac in range 1..30] [read\n   * image from input file] pic = (unsigned char*) malloc(3*width*height);\n   * initnet(pic,3*width*height,samplefac); learn(); unbiasnet(); [write output\n   * image header, using writecolourmap(f)] inxbuild(); write output image using\n   * inxsearch(b,g,r)\n   */\n\n  /*\n   * Network Definitions -------------------\n   */\n\n  var maxnetpos = netsize - 1;\n  var netbiasshift = 4;\n  /* bias for colour values */\n\n  var ncycles = 100;\n  /* no. of learning cycles */\n\n  /* defs for freq and bias */\n\n  var intbiasshift = 16;\n  /* bias for fractions */\n\n  var intbias = 1 << intbiasshift;\n  var gammashift = 10;\n  /* gamma = 1024 */\n\n  var gamma = 1 << gammashift;\n  var betashift = 10;\n  var beta = intbias >> betashift;\n  /* beta = 1/1024 */\n\n  var betagamma = intbias << gammashift - betashift;\n  /* defs for decreasing radius factor */\n\n  var initrad = netsize >> 3;\n  /* for 256 cols, radius starts */\n\n  var radiusbiasshift = 6;\n  /* at 32.0 biased by 6 bits */\n\n  var radiusbias = 1 << radiusbiasshift;\n  var initradius = initrad * radiusbias;\n  /* and decreases by a */\n\n  var radiusdec = 30;\n  /* factor of 1/30 each cycle */\n\n  /* defs for decreasing alpha factor */\n\n  var alphabiasshift = 10;\n  /* alpha starts at 1.0 */\n\n  var initalpha = 1 << alphabiasshift;\n  var alphadec;\n  /* biased by 10 bits */\n\n  /* radbias and alpharadbias used for radpower calculation */\n\n  var radbiasshift = 8;\n  var radbias = 1 << radbiasshift;\n  var alpharadbshift = alphabiasshift + radbiasshift;\n  var alpharadbias = 1 << alpharadbshift;\n  /*\n   * Types and Global Variables --------------------------\n   */\n\n  var thepicture;\n  /* the input image itself */\n\n  var lengthcount;\n  /* lengthcount = H*W*3 */\n\n  var samplefac;\n  /* sampling factor 1..30 */\n  // typedef int pixel[4]; /* BGRc */\n\n  var network;\n  /* the network itself - [netsize][4] */\n\n  var netindex = [];\n  /* for network lookup - really 256 */\n\n  var bias = [];\n  /* bias and freq arrays for learning */\n\n  var freq = [];\n  var radpower = [];\n\n  var NeuQuant = exports.NeuQuant = function NeuQuant(thepic, len, sample) {\n    var i;\n    var p;\n    thepicture = thepic;\n    lengthcount = len;\n    samplefac = sample;\n    network = new Array(netsize);\n\n    for (i = 0; i < netsize; i++) {\n      network[i] = new Array(4);\n      p = network[i];\n      p[0] = p[1] = p[2] = (i << netbiasshift + 8) / netsize;\n      freq[i] = intbias / netsize;\n      /* 1/netsize */\n\n      bias[i] = 0;\n    }\n  };\n\n  var colorMap = function colorMap() {\n    var map = [];\n    var index = new Array(netsize);\n\n    for (var i = 0; i < netsize; i++) index[network[i][3]] = i;\n\n    var k = 0;\n\n    for (var l = 0; l < netsize; l++) {\n      var j = index[l];\n      map[k++] = network[j][0];\n      map[k++] = network[j][1];\n      map[k++] = network[j][2];\n    }\n\n    return map;\n  };\n  /*\n   * Insertion sort of network and building of netindex[0..255] (to do after\n   * unbias)\n   * -------------------------------------------------------------------------------\n   */\n\n\n  var inxbuild = function inxbuild() {\n    var i;\n    var j;\n    var smallpos;\n    var smallval;\n    var p;\n    var q;\n    var previouscol;\n    var startpos;\n    previouscol = 0;\n    startpos = 0;\n\n    for (i = 0; i < netsize; i++) {\n      p = network[i];\n      smallpos = i;\n      smallval = p[1];\n      /* index on g */\n\n      /* find smallest in i..netsize-1 */\n\n      for (j = i + 1; j < netsize; j++) {\n        q = network[j];\n\n        if (q[1] < smallval) {\n          /* index on g */\n          smallpos = j;\n          smallval = q[1];\n          /* index on g */\n        }\n      }\n\n      q = network[smallpos];\n      /* swap p (i) and q (smallpos) entries */\n\n      if (i != smallpos) {\n        j = q[0];\n        q[0] = p[0];\n        p[0] = j;\n        j = q[1];\n        q[1] = p[1];\n        p[1] = j;\n        j = q[2];\n        q[2] = p[2];\n        p[2] = j;\n        j = q[3];\n        q[3] = p[3];\n        p[3] = j;\n      }\n      /* smallval entry is now in position i */\n\n\n      if (smallval != previouscol) {\n        netindex[previouscol] = startpos + i >> 1;\n\n        for (j = previouscol + 1; j < smallval; j++) netindex[j] = i;\n\n        previouscol = smallval;\n        startpos = i;\n      }\n    }\n\n    netindex[previouscol] = startpos + maxnetpos >> 1;\n\n    for (j = previouscol + 1; j < 256; j++) netindex[j] = maxnetpos;\n    /* really 256 */\n\n  };\n  /*\n   * Main Learning Loop ------------------\n   */\n\n\n  var learn = function learn() {\n    var i;\n    var j;\n    var b;\n    var g;\n    var r;\n    var radius;\n    var rad;\n    var alpha;\n    var step;\n    var delta;\n    var samplepixels;\n    var p;\n    var pix;\n    var lim;\n    if (lengthcount < minpicturebytes) samplefac = 1;\n    alphadec = 30 + (samplefac - 1) / 3;\n    p = thepicture;\n    pix = 0;\n    lim = lengthcount;\n    samplepixels = lengthcount / (3 * samplefac);\n    delta = samplepixels / ncycles | 0;\n    alpha = initalpha;\n    radius = initradius;\n    rad = radius >> radiusbiasshift;\n    if (rad <= 1) rad = 0;\n\n    for (i = 0; i < rad; i++) radpower[i] = alpha * ((rad * rad - i * i) * radbias / (rad * rad));\n\n    if (lengthcount < minpicturebytes) step = 3;else if (lengthcount % prime1 !== 0) step = 3 * prime1;else {\n      if (lengthcount % prime2 !== 0) step = 3 * prime2;else {\n        if (lengthcount % prime3 !== 0) step = 3 * prime3;else step = 3 * prime4;\n      }\n    }\n    i = 0;\n\n    while (i < samplepixels) {\n      b = (p[pix + 0] & 0xff) << netbiasshift;\n      g = (p[pix + 1] & 0xff) << netbiasshift;\n      r = (p[pix + 2] & 0xff) << netbiasshift;\n      j = contest(b, g, r);\n      altersingle(alpha, j, b, g, r);\n      if (rad !== 0) alterneigh(rad, j, b, g, r);\n      /* alter neighbours */\n\n      pix += step;\n      if (pix >= lim) pix -= lengthcount;\n      i++;\n      if (delta === 0) delta = 1;\n\n      if (i % delta === 0) {\n        alpha -= alpha / alphadec;\n        radius -= radius / radiusdec;\n        rad = radius >> radiusbiasshift;\n        if (rad <= 1) rad = 0;\n\n        for (j = 0; j < rad; j++) radpower[j] = alpha * ((rad * rad - j * j) * radbias / (rad * rad));\n      }\n    }\n  };\n  /*\n   ** Search for BGR values 0..255 (after net is unbiased) and return colour\n   * index\n   * ----------------------------------------------------------------------------\n   */\n\n\n  var map = exports.map = function map(b, g, r) {\n    var i;\n    var j;\n    var dist;\n    var a;\n    var bestd;\n    var p;\n    var best;\n    bestd = 1000;\n    /* biggest possible dist is 256*3 */\n\n    best = -1;\n    i = netindex[g];\n    /* index on g */\n\n    j = i - 1;\n    /* start at netindex[g] and work outwards */\n\n    while (i < netsize || j >= 0) {\n      if (i < netsize) {\n        p = network[i];\n        dist = p[1] - g;\n        /* inx key */\n\n        if (dist >= bestd) i = netsize;\n        /* stop iter */\n        else {\n            i++;\n            if (dist < 0) dist = -dist;\n            a = p[0] - b;\n            if (a < 0) a = -a;\n            dist += a;\n\n            if (dist < bestd) {\n              a = p[2] - r;\n              if (a < 0) a = -a;\n              dist += a;\n\n              if (dist < bestd) {\n                bestd = dist;\n                best = p[3];\n              }\n            }\n          }\n      }\n\n      if (j >= 0) {\n        p = network[j];\n        dist = g - p[1];\n        /* inx key - reverse dif */\n\n        if (dist >= bestd) j = -1;\n        /* stop iter */\n        else {\n            j--;\n            if (dist < 0) dist = -dist;\n            a = p[0] - b;\n            if (a < 0) a = -a;\n            dist += a;\n\n            if (dist < bestd) {\n              a = p[2] - r;\n              if (a < 0) a = -a;\n              dist += a;\n\n              if (dist < bestd) {\n                bestd = dist;\n                best = p[3];\n              }\n            }\n          }\n      }\n    }\n\n    return best;\n  };\n\n  var process = exports.process = function process() {\n    learn();\n    unbiasnet();\n    inxbuild();\n    return colorMap();\n  };\n  /*\n   * Unbias network to give byte values 0..255 and record position i to prepare\n   * for sort\n   * -----------------------------------------------------------------------------------\n   */\n\n\n  var unbiasnet = function unbiasnet() {\n    var i;\n    var j;\n\n    for (i = 0; i < netsize; i++) {\n      network[i][0] >>= netbiasshift;\n      network[i][1] >>= netbiasshift;\n      network[i][2] >>= netbiasshift;\n      network[i][3] = i;\n      /* record colour no */\n    }\n  };\n  /*\n   * Move adjacent neurons by precomputed alpha*(1-((i-j)^2/[r]^2)) in\n   * radpower[|i-j|]\n   * ---------------------------------------------------------------------------------\n   */\n\n\n  var alterneigh = function alterneigh(rad, i, b, g, r) {\n    var j;\n    var k;\n    var lo;\n    var hi;\n    var a;\n    var m;\n    var p;\n    lo = i - rad;\n    if (lo < -1) lo = -1;\n    hi = i + rad;\n    if (hi > netsize) hi = netsize;\n    j = i + 1;\n    k = i - 1;\n    m = 1;\n\n    while (j < hi || k > lo) {\n      a = radpower[m++];\n\n      if (j < hi) {\n        p = network[j++];\n\n        try {\n          p[0] -= a * (p[0] - b) / alpharadbias;\n          p[1] -= a * (p[1] - g) / alpharadbias;\n          p[2] -= a * (p[2] - r) / alpharadbias;\n        } catch (e) {} // prevents 1.3 miscompilation\n\n      }\n\n      if (k > lo) {\n        p = network[k--];\n\n        try {\n          p[0] -= a * (p[0] - b) / alpharadbias;\n          p[1] -= a * (p[1] - g) / alpharadbias;\n          p[2] -= a * (p[2] - r) / alpharadbias;\n        } catch (e) {}\n      }\n    }\n  };\n  /*\n   * Move neuron i towards biased (b,g,r) by factor alpha\n   * ----------------------------------------------------\n   */\n\n\n  var altersingle = function altersingle(alpha, i, b, g, r) {\n    /* alter hit neuron */\n    var n = network[i];\n    n[0] -= alpha * (n[0] - b) / initalpha;\n    n[1] -= alpha * (n[1] - g) / initalpha;\n    n[2] -= alpha * (n[2] - r) / initalpha;\n  };\n  /*\n   * Search for biased BGR values ----------------------------\n   */\n\n\n  var contest = function contest(b, g, r) {\n    /* finds closest neuron (min dist) and updates freq */\n\n    /* finds best neuron (min dist-bias) and returns position */\n\n    /* for frequently chosen neurons, freq[i] is high and bias[i] is negative */\n\n    /* bias[i] = gamma*((1/netsize)-freq[i]) */\n    var i;\n    var dist;\n    var a;\n    var biasdist;\n    var betafreq;\n    var bestpos;\n    var bestbiaspos;\n    var bestd;\n    var bestbiasd;\n    var n;\n    bestd = ~(1 << 31);\n    bestbiasd = bestd;\n    bestpos = -1;\n    bestbiaspos = bestpos;\n\n    for (i = 0; i < netsize; i++) {\n      n = network[i];\n      dist = n[0] - b;\n      if (dist < 0) dist = -dist;\n      a = n[1] - g;\n      if (a < 0) a = -a;\n      dist += a;\n      a = n[2] - r;\n      if (a < 0) a = -a;\n      dist += a;\n\n      if (dist < bestd) {\n        bestd = dist;\n        bestpos = i;\n      }\n\n      biasdist = dist - (bias[i] >> intbiasshift - netbiasshift);\n\n      if (biasdist < bestbiasd) {\n        bestbiasd = biasdist;\n        bestbiaspos = i;\n      }\n\n      betafreq = freq[i] >> betashift;\n      freq[i] -= betafreq;\n      bias[i] += betafreq << gammashift;\n    }\n\n    freq[bestpos] += beta;\n    bias[bestpos] -= betagamma;\n    return bestbiaspos;\n  };\n\n  NeuQuant.apply(this, arguments);\n  return exports;\n};"